<template>
    <v-app>
        <v-content>
            <v-container class="fill-height" fluid>
                <v-row align="center" justify="center">
                    <v-col cols="12" sm="8" md="4">
                        <v-card class="elevation-12">
                            <v-toolbar color="primary" dark flat>
                                <v-toolbar-title>登录</v-toolbar-title>
                            </v-toolbar>
                            <v-card-text>
                                <v-form>
                                    <v-text-field label="用户名" prepend-icon="mdi-account" type="text" counter
                                                  v-model="username"/>
                                    <v-text-field label="密码" prepend-icon="mdi-lock" type="password" counter
                                                  v-model="password"/>
                                </v-form>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="primary" @click="login">登录</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-col>
                </v-row>
            </v-container>
        </v-content>
    </v-app>
</template>

<script>
    export default {
        name: "Login",
        props: {
            source: String
        },
        data: function () {
            return {
                username: "",
                password: ""
            }
        },
        methods: {
            login: function () {
                alert("用户名："+this.username + "，密码：" + this.password)
                this.$router.replace("/home")
            }
        }
    }
</script>

